import {
  mapActions,
  mapGetters
} from 'vuex';
import MusicPlayerI18n from './MusicPlayer.i18n.js';

export default {

  components: {},

  props: {},

  computed: {
    ...mapGetters(['currentMusic']),
    timer() {
      return this.formatDuration(this.currentTime) + ' / ' + this.formatDuration(this.totalDuration)
    },
    trackInfo() {
      return this.currentMusic.artist + " - " + this.currentMusic.title
    }
  },

  data() {
    return {
      audioPlayer: null,
      audioSource: "http://127.0.0.1:8000/api/audio/100",
      audioPlaying: false,
      totalDuration: 0,
      currentTime: 0,
      audioLoaded: false,
      listenerActive: false
    };
  },

  watch: {
    currentMusic(pValue) {
      if (!this.audioPlaying) {
        this.pause()
      }

      //this.audioPlaying = false
      this.currentTime = 0
      this.totalDuration = 0
      this.listenerActive = false
      this.audioSource = process.env.VUE_APP_API_URL + '/audio/' + pValue.id
      this.audioPlayer.load();
      this.play()
    },
    audioPlaying(pValue) {
      //this.initSlider();
      if (!this.listenerActive) {
        this.listenerActive = true;
        this.audioPlayer.addEventListener("timeupdate", this.playbackListener);
      }
    },
    currentTime(pValue) {
      let diff = Math.abs(pValue - this.audioPlayer.currentTime);
      if (diff > 0.5) {
        this.audioPlayer.currentTime = pValue;
      }
    }
  },

  methods: {

    /**
     * Fonction pour formatter des secondes en minutes.
     * @param {*} value 
     * @returns 
     */
    formatDuration(pValue) {
      let minutes = Math.floor(pValue / 60);
      let seconds = Math.floor(pValue - minutes * 60);
      if (seconds === 0) {
        seconds = '00'
      }
      if (seconds < 10 && seconds > 0) {
        seconds = '0' + seconds
      }
      return minutes + ":" + seconds;
    },

    /**
     * Fonction pour récupérer le lecteur audio grâce à son id.
     */
    getAudioPlayer() {
      this.audioPlayer = document.getElementById('audio-player');
    },

    /**
     * Fonction pour initialiser le slider.
     */
    initSlider() {
      if (this.audioPlayer) {
        this.totalDuration = Math.round(this.audioPlayer.duration);
      }
    },

    /**
     * Fonction permettant de savoir si la musique est en lecture.
     * @returns
     */
    isAudioPlaying() {
      let isPlaying = this.audioPlayer && !this.audioPlayer.paused
      return isPlaying
    },

    playbackListener(e) {
      this.currentTime = this.audioPlayer.currentTime;
    },

    cleanupListeners() {
      this.audioPlayer.removeEventListener("timeupdate", this.playbackListener);
    },

    /**
     * Fonction pour jouer/mettre en pause la musique.
     */
    toggleAudio() {
      if (this.audioPlayer.paused) {
        this.play();
      } else {
        this.pause();
      }
      this.isAudioPlaying();
      this.$forceUpdate();
    },

    /**
     * Fonction pour mettre en puse la musique.
     */
    pause() {
      this.audioPlayer.pause();
      this.audioPlaying = false;
      this.cleanupListeners();
    },

    /**
     * Fonction pour jouer la musique.
     */
    play() {
      this.audioPlayer.play();
      this.audioPlaying = true;
    }
  },


  mounted() {
    this.getAudioPlayer();
    this.audioPlayer.addEventListener(
      "loadedmetadata",
      function (e) {
        this.initSlider()
      }.bind(this)
    );

    this.audioPlayer.addEventListener(
      "canplay",
      function (e) {
        this.audioLoaded = true;
      }.bind(this)
    );
  },

  i18n: MusicPlayerI18n,

};