import axios from 'axios'

const state = {
  audioFile: null,
  audioLoading: false
}

const actions = {
  /**
   * Service pour récupérer un fichier audio.
   * @param {*} param0 
   * @returns 
   */
  getAudioFile({
    commit,
  }, musicId) {
    commit('setAudioLoading', true);
    const audioSource = document.getElementById("my-audio");
    return new Promise((resolve, reject) => {
      const token = localStorage.getItem('token');
      const AuthStr = 'Bearer ' + token;
      axios.get(process.env.VUE_APP_API_URL + '/audio/' + musicId, {
        headers: {
          Accept: 'audio/mpeg',
          Authorization: AuthStr,
        }
      }).then(response => {
          
        //commit('setAudioFile', response.data);
        commit('setAudioLoading', false);
        resolve(response);
      }).catch(error => {
        commit('setAudioLoading', false);
        reject(error.response);
      })
    })
  },
}


const getters = {
  audioFile: state => state.audioFile,
  audioLoading: state => state.audioLoading
}

const mutations = {
  setAudioFile(state, audioFile) {
    state.audioFile = audioFile;
  },
  setAudioLoading(state, audioLoading) {
    state.audioLoading = audioLoading;
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}
