import Vue from 'vue'
import VueRouter from 'vue-router'
import TotoMusique from '../views/TotoMusique/TotoMusique.vue'
import Login from '../views/Login/Login.vue'
import Home from '../views/Home/Home.vue'
import AllMusic from '../views/AllMusic/AllMusic.vue'
import Artists from '../views/Artists/Artists.vue'
import Playlists from '../views/Playslists/Playslists.vue'
import store from './../store'

Vue.use(VueRouter)

const routes = [

  // Public part
  {
    path: '/login',
    component: Login,
    meta: {
      notAllowedIfLogged: true
    }
  },
  // Logged part
  {
    path: '/',
    component: TotoMusique,
    meta: {
      requiresAuth: true
    },
    children: [{
        path: '',
        name: 'Home',
        component: Home
      },
      {
        path: '/all',
        name: 'All',
        component: AllMusic
      },
      {
        path: '/artists',
        name: 'Artists',
        component: Artists
      },
      {
        path: '/playlists',
        name: 'Playlists',
        component: Playlists
      },
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // Check if user is loggin
    if (localStorage.getItem('token')) {
      // Get info about the user
      store.dispatch('getUser').then(res => {
        next();
        return
      }).catch(err => {
        next('/login');
        return;
      })

    } else {
      next('/login');
    }
  } else if (to.matched.some(record => record.meta.notAllowedIfLogged) && localStorage.getItem('token')) {
    next('/')
    return;
  } else {
    next();
  }
})

export default router