import axios from 'axios'

const state = {
  musics: [],
  musicsLoading: false,
  currentMusic: null,

}

const actions = {
  /**
   * Service pour récupérer toutes les musiques.
   * @param {*} param0 
   * @returns 
   */
  getMusics({
    commit,
  }) {
    commit('setMusicsLoading', true);
    return new Promise((resolve, reject) => {
      const token = localStorage.getItem('token');
      const AuthStr = 'Bearer ' + token;
      axios.get(process.env.VUE_APP_API_URL + '/musics', {
        headers: {
          Accept: 'application/json',
          Authorization: AuthStr,
        }
      }).then(response => {
        commit('setMusics', response.data);
        commit('setMusicsLoading', false);
        resolve(response);
      }).catch(error => {
        commit('setMusicsLoading', false);
        reject(error.response);
      })
    })
  },
}


const getters = {
  musics: state => state.musics,
  musicsLoading: state => state.musicsLoading,
  currentMusic: state => state.currentMusic
}

const mutations = {
  setMusics(state, musics) {
    state.musics = musics;
  },
  setMusicsLoading(state, musicsLoading) {
    state.musicsLoading = musicsLoading;
  },
  setCurrentMusic(state, currentMusic) {
    state.currentMusic = currentMusic
  },
  
}

export default {
  state,
  getters,
  actions,
  mutations
}
